body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App{
  height: 100vh;
}
header {
  background-color: black;
  color: white;
  display: flex;
  justify-content: space-between;
}
footer{
  background-color: black;
  height: 15vh;
  text-align: center;
  color: white;
  padding: 1rem;
}
img.profile{
  margin-top: 0.5rem;
  width: 60px; 
  height: 60px;
  border-radius: 40%;
}
.userPic{
  display: flex;
}

section{
  display: flex; 

}
table{
  display: list-item;
  width: 50%;
}
form{
  width: 50%;
}
.tabla{
  padding: 2rem;
  margin: auto;
  display: flex;
  width: 80%;
}
